// Permissions
// 登录
const CAN_LOGIN = 0b1000000000000000000000000
// 登出
const CAN_LOGGOUT = 0b0100000000000000000000000
// 踢登
const CAN_KICKOUT = 0b0010000000000000000000000
// 获取在线用户
const CAN_GETONLINEUSER = 0b0001000000000000000000000
// 借出设备
const CAN_LEND = 0b0000100000000000000000000
// 归还设备
const CAN_RETURN = 0b0000010000000000000000000
// 帮借
const CAN_HELPLEND = 0b0000001000000000000000000
// 帮还
const CAN_HELPRETURN = 0b0000000100000000000000000
// 设备清点
const CAN_EQCHECK = 0b0000000010000000000000000
// 获取借出记录
const CAN_GETRECORD = 0b0000000001000000000000000
// 编辑借出记录
const CAN_EDITRECORD = 0b0000000000100000000000000
// 删除借出记录
const CAN_DELRECORD = 0b0000000000010000000000000
// 获取设备
const CAN_GETEQ = 0b0000000000001000000000000
// 添加设备
const CAN_ADDEQ = 0b0000000000000100000000000
// 编辑设备
const CAN_EDITEQ = 0b0000000000000010000000000
// 删除设备
const CAN_DELEQ = 0b0000000000000001000000000
// 获取用户
const CAN_GETUSER = 0b0000000000000000100000000
// 添加用户
const CAN_ADDUSER = 0b0000000000000000010000000
// 编辑用户
const CAN_EDITUSER = 0b0000000000000000001000000
// 删除用户
const CAN_DELUSER = 0b0000000000000000000100000
// 获取日志
const CAN_GETLOG = 0b0000000000000000000010000
// 看监控
const CAN_WATCHCCTV = 0b0000000000000000000001000
// 回放监控
const CAN_REPLAYCCTV = 0b0000000000000000000000100
// 随机抽人
const CAN_RANDOMPEOPLE = 0b0000000000000000000000010
// 晚修管理
const CAN_WXGL = 0b0000000000000000000000001

const NeedPermissionsPages = [
  {
    name: 'lend',
    Permissions: CAN_LEND,
  },
  {
    name: 'Return',
    Permissions: CAN_RETURN,
  },
  {
    name: 'SystemLog',
    Permissions: CAN_GETLOG,
  },
  {
    name: 'RequestLog',
    Permissions: CAN_GETLOG,
  },
  {
    name: 'BehaviorLog',
    Permissions: CAN_GETLOG,
  },
  {
    name: 'EqList',
    Permissions: CAN_GETEQ,
  },
  {
    name: 'EqCheck',
    Permissions: CAN_EQCHECK,
  },
  {
    name: 'UserManage',
    Permissions: CAN_GETUSER,
  },
  {
    name: 'CCTV',
    Permissions: CAN_WATCHCCTV,
  },
  {
    name: 'WXGL',
    Permissions: CAN_WXGL,
  },
]


export default {
  props:{
    page:{
      type:String,
      default: '',
    },
    ChangePageUrl:{
      type: Function,
      default: null,
    },
    UserPermissions:{
      type: Array,
      default: [],
    },
  },
  
  render: function (h) {
    // const Tag = `h${this.number}`;
    // return <Tag>{this.$slots.default}</Tag>;
    // return <lend/>
    // return <div>{this.page}</div>
    // return <updating/>

    /**主函数 */

    console.log(this.UserPermissions)

    if (this.page == '') {
      return <ParameterError/>
    }
    if (update) {
      return <Updating/>
    }
    if (this.page == "PageSmall") {
      return <PageSmall/>
    }
    if (window.innerWidth < config.minWidth) {
      return <PageSmall/>
    }

    // for (const index in NeedPermissionsPages) {
    //   if (Object.hasOwnProperty.call(NeedPermissionsPages, index)) {
    //     const element = NeedPermissionsPages[index];
    //     if (element.name == this.page) {
    //       // 当前选择的页面需要权限
    //       if (VerifyLocalPermissions(element.Permissions) == false) {
    //           console.warn("无权限")
    //           return <Nopermissions/>
    //       }
    //       else{
    //         break
    //       }
    //       // console.log(await VerifyPermissions(element.Permissions))
    //       // if (await VerifyPermissions(element.Permissions) == false) {
    //       //   console.warn("无权限")
    //       //   return <Nopermissions/>
    //       // }
    //     }
    //   }
    // }
    
    try{
      if (WorkingPage.indexOf(this.page) != -1) {
        return <PUpdating></PUpdating>
      }
      const a = pagelist[this.page]
      if (a) {
        const B = a
        return <B ChangePageUrl={this.ChangePageUrl}></B>
      }
      return <NotFound/>
    }
    catch(err){
      console.log(err)
      return <Error msg={err}></Error>
    }
  },
};






